import "./Nav.css";
function Nav(){
    const changenavbarcolor=()=>{
        if(window.scrollY>=80){
            document.getElementsByTagName("nav")[0].classList.add("sticky");
        }else{
                document.getElementsByTagName("nav")[0].classList.remove("sticky");
        }
    }
    window.addEventListener('scroll',changenavbarcolor);

    return(
        <>
          <nav className="col-12 nav position-fixed top-0" >
            <div className="mx-auto container row justify-content-between align-items-center nav">
                    <div className="d-lg-none ham col-2" onClick={()=>{
                        let ab=document.getElementById("some");
                        ab.style.left="0%";
                    }}>
                      <i class="bi bi-list text-white"></i>
                    </div>
                    <div className="logo1">
                        <a href="#"><img className="img-fluid" src="images/caverta-logo.png" alt="" title="" /></a>
                    </div>
                    <ul className="col-lg-7 col-xl-6 list d-none d-lg-flex justify-content-between align-items-center m-0 p-0">
                        <li><a className="text-uppercase" href="#">Home</a></li>
                        <li><a className="text-uppercase" href="#">Menu</a></li>
                        <li><a className="text-uppercase" href="#">Pages</a></li>
                        <li><a className="text-uppercase" href="#">Reservation</a></li>
                        <li><a className="text-uppercase" href="#">Blog</a></li>
                        <li><a className="text-uppercase" href="#">Team</a></li>
                        <li><a className="text-uppercase" href="#">Gallery</a></li>
                        <li><a className="text-uppercase" href="#">Contact</a></li>
                    </ul>
                    <div className="col-3 d-flex justify-content-end align-items-center">
                        <ul className="social d-flex flex-wrap justify-content-evenly  m-0 p-0">
                          <li><i class="bi bi-facebook "></i></li>
                          <li><i class="bi bi-twitter "></i></li>
                          <li><i class="bi bi-instagram "></i></li>
                          <li><i class="bi bi-linkedin "></i></li>
                        </ul>   
                        <a className=" d-lg-flex book-btn d-none justify-content-center align-items-center text-uppercase ms-1" href="#">Book A Table</a>
                    </div>
                </div>
                <div id="some" className=" d-lg-none col-12 col-sm-6 px-4 left-list position-absolute px-3">
                    <div className="butt my-3 " onClick={()=>{
                         let ab=document.getElementById("some");
                         ab.style.left="-100%";
                    }}>
                        <i class="bi bi-x-lg text-white fs-3"></i>
                    </div>
                    <ul className="col-7 mx-auto mt-2 list d-flex flex-column justify-content-between align-items-start m-0 p-0">
                        <li className="my-2"><a className="text-uppercase text-white" href="#">Home</a></li>
                        <li className="my-2"><a className="text-uppercase" href="#">Menu</a></li>
                        <li className="my-2"><a className="text-uppercase" href="#">Pages</a></li>
                        <li className="my-2"><a className="text-uppercase" href="#">Reservation</a></li>
                        <li className="my-2"><a className="text-uppercase" href="#">Blog</a></li>
                        <li className="my-2"><a className="text-uppercase" href="#">Team</a></li>
                        <li className="my-2"><a className="text-uppercase" href="#">Gallery</a></li>
                        <li className="my-2"><a className="text-uppercase" href="#">Contact</a></li>
                    </ul>
                    <div className="col-7 cont mt-3 mx-auto d-flex flex-column justify-content-start align-items-start">
                        <a className=" d-lg-block book-btn d-flex justify-content-center align-items-center text-uppercase " href="#">Book A Table</a>
                        <p className="text-start mt-2 mb-0">58 Ralph Ave</p>
                        <p className="text-left my-2">New York, New York 1111 </p>
                        <p className="mb-0"><a className=" d-lg-block  d-flex justify-content-center align-items-center text-uppercase text-start fs-3 my-1 " href="tel:8528140020">+1 800 000 111</a></p>
                        <p className="mb-0"><a className=" d-lg-block  d-flex justify-content-center align-items-center fs-5 " href="mailto:pabhiyadav82@gmail.com">contact@example.com</a></p>

                        <ul className="social my-3 d-flex flex-wrap justify-content-evenly col-8 m-0 p-0">
                            <li><i class="bi bi-facebook "></i></li>
                            <li><i class="bi bi-twitter "></i></li>
                            <li><i class="bi bi-instagram "></i></li>
                            <li><i class="bi bi-linkedin "></i></li>
                        </ul>
                    </div>
                </div>
          </nav>  
        </>
    );
}
export default Nav;